@use '../../globals/helpers' as *;

$radio-spacing-text: 8px;
$radio-circle-width: 24px;
$radio-circle-height: 24px;
$radio-border-radius: 16px;
$radio-circle-pointer-width: 10px;
$radio-circle-pointer-height: 10px;

:host {
  display: flex;
}
.radio {
  display: flex;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  flex-wrap: nowrap;

  [type='radio'] {
    display: none;
    &:focus {
      outline: 0;
    }
  }

  &__circle {
    @include animation();
    @include flex-align-middle();
    width: $radio-circle-width;
    height: $radio-circle-height;
    flex-shrink: 0;
    border: 2px solid $color-content-default;
    padding: 4px;
    border-radius: 100%;
    box-sizing: border-box;
    background: transparent;
    position: relative;

    &__pointer {
      @include animation();
      border-radius: 100%;
      background: transparent;
      height: $radio-circle-pointer-height;
      width: $radio-circle-pointer-width;
    }

    .hover {
      width: 0;
      height: 0;
      opacity: 0;
    }

    .focus:focus-visible {
      display: flex;
      position: absolute;
      border: 2px solid $color-focus;
      border-radius: 4px;
      padding: 4px;
      width: 100%;
      height: 100%;
      outline: none;
    }
  }

  &:hover {
    border-color: $color-content-disable;
    .hover {
      display: flex;
      background-color: $color-hover;
      position: absolute;
      width: 36px;
      height: 36px;
      opacity: 1;
      border-radius: 24px;
      transition:
        width 0.2s,
        height 0.2s;
    }
  }

  &__text {
    @include no-select();
    padding-left: $radio-spacing-text;
    color: $color-content-default;
  }

  /** State Checked */
  &__input[type='radio']:checked ~ &__circle {
    background: transparent;
    border-color: $color-content-default;

    .radio__circle__pointer {
      background-color: $color-primary;
    }

    &:hover {
      border-color: $color-content-default;

      .radio__circle__pointer {
        background-color: $color-primary;
      }
    }
  }

  /** State Disabled */
  &__input[type='radio']:disabled ~ &__circle {
    border-color: $color-content-disable;
    background-color: $color-surface-3;

    .radio__circle__pointer {
      background-color: transparent;
    }
  }
  &__input[type='radio']:disabled:hover ~ &__circle {
    border-color: $color-content-disable;
    background-color: $color-surface-3;

    .radio__circle__pointer {
      background-color: transparent;
    }
  }
  &__input[type='radio']:disabled:checked ~ &__circle {
    border-color: $color-content-disable;
    background-color: $color-surface-3;

    .radio__circle__pointer {
      background-color: $color-content-default;
    }
  }
  &__input[type='radio']:disabled:checked:hover ~ &__circle {
    border-color: $color-content-disable;
    background-color: $color-surface-3;

    .radio__circle__pointer {
      background-color: $color-content-default;
    }
  }

  &__input[type='radio']:disabled ~ &__text {
    color: $color-content-disable;
    cursor: not-allowed;
  }
  &__input[type='radio']:disabled ~ &__circle {
    cursor: not-allowed;
  }
}
